/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { InspectorControls, useBlockProps } from '@wordpress/block-editor';

import { useSelect, select } from '@wordpress/data';
import { Dashicon, Panel, PanelBody, PanelRow, SelectControl } from '@wordpress/components';
import { useEffect, useState } from '@wordpress/element';
import { blockTable, more } from '@wordpress/icons';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
export default function Edit({
	className,
	attributes, 
	setAttributes 
}) {
	const { getEntityRecords } = select('core');

	const tableTypes = [
		'think-tank-archive',
		'single-think-tank',
		'donor-archive',
		'single-donor',
		'top-10',
	];

	// const setTableType = ( value ) => {
	// 	setAttributes( {
	// 		tableType: value
	// 	} );

	// 	setTableTypeState( value );
	// }

	// const setThinkTank = ( value ) => {
	// 	setAttributes( {
	// 		thinkTank: value
	// 	} );

	// 	setThinkTankState( value );
	// }

	// const setDonor = ( value ) => {
	// 	setAttributes( {
	// 		donor: value
	// 	} );

	// 	setDonorState( value );
	// }

	// const setDonationYear = ( value ) => {
	// 	setAttributes( {
	// 		donationYear: value
	// 	} );

	// 	setDonationYearState( value );
	// }

	// const setDonorType = ( value ) => {
	// 	setAttributes( {
	// 		donorType: value
	// 	} );

	// 	setDonorTypeState( value );
	// }

	const [ tableType, setTableType ] = useState( attributes.tableType || '' );
	const [ thinkTank, setThinkTank ] = useState( attributes.thinkTank || '' );
	const [ donor, setDonor ] = useState( attributes.donor || '' );
	const [ donationYear, setDonationYear ] = useState( attributes.donationYear || '' );
	const [ donorType, setDonorType ] = useState( attributes.donorType || '' );

	useEffect( () => {
		setAttributes( {
			tableType: tableType,
			thinkTank: thinkTank,
			donor: donor,
			donationYear: donationYear,
			donorType: donorType,
		} );
	}, [ tableType, thinkTank, donor, donationYear, donorType ] );

	const currentYear = new Date().getFullYear().toString();

	const isThinkTankType = () => {
		return ( 'think-tank-archive' === tableType || 'single-think-tank' === tableType );
	}

	const isDonorType = () => {
		return ( 'donor-archive' === tableType || 'single-donor' === tableType );
	}

	const optionsMap = ( terms ) => {
		let options = [];

		if( terms ) {
			options.push( { 
				value: 0, 
				label: __( 'Select', 'data-tables' )
			} );
			terms.forEach( ( year ) => {
				options.push( {
					value: year.slug,
					label: year.name
				} )
			} );
			
		} else {
			options.push( {
				value: 0, 
				label: __( 'Loading...', 'data-tables' )
			} )
		}
		return options;
	}

	const tableTypeOptions =[
		{ 
			value: 'think-tank-archive', 
			label: __( 'Think Tanks Archive', 'data-tables' ) 
		},
		{ 
			value: 'donor-archive', 
			label: __( 'Donors Archive', 'data-tables' ) 
		},
		{ 
			value: 'single-think-tank', 
			label: __( 'Single Think Tank', 'data-tables' ) 
		},
		{ 
			value: 'single-donor', 
			label: __( 'Single Think Tank Archive', 'data-tables' ) 
		},
	];
	
	const { thinkTanks } = useSelect( ( select ) => {
		const taxonomy = 'think_tank';
		const query = {
			'orderby': 'name',
			'per_page': 100,
		}
		return {
			thinkTanks: getEntityRecords( 'taxonomy', taxonomy, query )
		}
	});

	const { donors } = useSelect( ( select ) => {
		const taxonomy = 'donor';
		const query = {
			'orderby': 'name',
			'per_page': -1,
		}
		return {
			donors: getEntityRecords( 'taxonomy', taxonomy, query )
		}
	});

	const { years } = useSelect( ( select ) => {
		const taxonomy = 'donation_year';
		const query = {
			'orderby': 'name',
			'order': 'desc',
			'per_page': 20,
		}
		return {
			years: getEntityRecords( 'taxonomy', taxonomy, query )
		}
	});

	const { donorTypes } = useSelect(( select ) => {
		const taxonomy = 'donor_type';
		const query = {
			'orderby': 'name',
			'per_page': -1,
		}
		return {
			donorTypes: getEntityRecords( 'taxonomy', taxonomy, query )
		}
	});

	const getTableType = () => {
		const slug = attributes.tableType;
		if( slug ) {
			const result = tableTypeOptions.find( obj => {
				return obj.value === slug;
			} );
			if( result ) {
				return result.label;
			}
		}
		return '';
	}

	const thinkTankOptions = optionsMap( thinkTanks );

	const donorOptions = optionsMap( donors );

	const yearOptions = optionsMap( years );

	const donorTypeOptions = optionsMap( donorTypes );

	const TableTypeSelector = () => {
		return (
			<PanelRow>
				<SelectControl 
					label={ __( 'Select a Table Type', 'data-tables' ) } 
					options={ tableTypeOptions }
					value={  attributes.tableType }
					onChange={ ( value ) => setTableType( value ) }
				/>
			</PanelRow>
		);
	};

	const ThinkTankSelector = () => {
		if( isThinkTankType() ) {
			return (
				<PanelRow>
					<SelectControl 
						label={ __( 'Select a Think Tank', 'data-tables' ) } 
						options={ thinkTankOptions } 
						value={  attributes.thinkTank }
						onChange={ ( value ) => setThinkTank( value ) }
					/>
				</PanelRow>
			);			
		}
	};

	const DonorSelector = () => {
		if( isDonorType() ) {
			return (
				<PanelRow>
					<SelectControl 
						label={ __( 'Select a Donor', 'data-tables' ) } 
						options={ donorOptions } 
						value={  attributes.donor }
						onChange={ ( value ) => setDonor( value ) }
					/>
				</PanelRow>
			);
		}
	};

	const YearSelector = () => {
		return (
			<PanelRow>
				<SelectControl 
					label={ __( 'Select a Year', 'data-tables' ) } 
					options={ yearOptions }
					value={ attributes.donationYear }
					onChange={ ( value ) => setDonationYear( value ) }
				/>
			</PanelRow>
		);
	};

	const DonorTypeSelector = () => {
		return (
			<PanelRow>
				<SelectControl 
					label={ __( 'Select a Donor Type', 'data-tables' ) } 
					options={ donorTypeOptions } 
					value={  attributes.donorType }
					onChange={ ( value ) => setDonorType( value ) }
				/>
			</PanelRow>
		);
	};

	const OptionsPanel = () => (
		<InspectorControls>
			<PanelBody 
				title={ __( 'Data Table Options', 'data-tables' ) } 
				initialOpen={ true }
				id="data-table-options"
			>
				<TableTypeSelector />

				<ThinkTankSelector />

				<DonorSelector />

				<YearSelector />

				<DonorTypeSelector />
			</PanelBody>
		</InspectorControls>
	);

	const tableTypeName = getTableType();

	return (
		<>
			<OptionsPanel />

			<div { ...useBlockProps() }>
				{ <Dashicon icon="editor-table" /> } { `${tableTypeName} Will be Displayed.` || __( 'Select a Table Type to Display.' ) }
			</div>
		</>

	);
}
